.Card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    min-width: 45%;
    max-width: 60%;
    margin-top: 30px;
  }
  
  .Card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .Container {
    padding: 2px 16px;
    overflow: auto;
    padding-bottom: 10px;
  }